import React from 'react';
import styled from 'styled-components';

const Experience = () => {
    return (
        <WorkWrapper>
        <div id="experience" style={{height:"100vh"}} className="main-work">
        <h3 className="text-title">Experience</h3>
             <div className="container">
                      <div className="item item-1">
                        <div className="card h-100">
                        <div className="card-title">
                          Cultural Infusion
                          </div>
                        <div className="card-body">
                        Developed a web application for the internal use of the organization. Used PHP for the back end, 
                        HTML5/CSS3/JavaScript/JQuery for the front end and SQL Server DB. Used SQL Server 
                        Integration Service(SSIS) for data integration and migration. Created end-user testing module and provided user training.
                          </div>
                          <div className="card-footer">
                          HTML CSS JavaScrpt PHP MSBI(SSIS)
                          </div>
                          </div>
                      </div>
                      <div className="item item-2">
                        <div className="card h-100">
                        <div className="card-title">
                        Dream PTE pvt ltd
                          </div>
                        <div className="card-body">
                          Redesigned the existing Javascript web application using Reactjs and styled components. Improved the speed and user
                          experience of the web application. Prepared the documentation and provided the user training.
                        </div>
                          <div className="card-footer">
                          ReactJS HTML CSS Styled-Components
                          </div>
                          </div>
                      </div>
            </div>
        </div>
        </WorkWrapper>
    );
};


export default Experience;

const WorkWrapper=styled.div`
.main-work{
  margin:60% 0 10px 0;  
  height: 100vh;  
  }
.container{
    background-color: var(--MainBackground) !important;
    display: grid;
    width: 100%;
    
    //Using auto-fill and auto-fit
    grid-template-rows: repeat(2, minmax(150px, min-content));
    grid-template-columns: repeat(auto-fill,100px);
    grid-template-columns: repeat(auto-fit,100px);
    grid-template-columns: repeat(auto-fit,minmax(200px, 1fr));
    grid-auto-rows: 150px;
}
.item{
      padding: 50px;
      background-color: var(--MainBackground) !important;
    }
    
  .card {
    background-color:var(--LightBackground) !important;
    color: var(--Font2) !important;
    font-family: "Courier", !important;
    -webkit-box-shadow: 0 10px 6px -6px var(--Font3);
    -moz-box-shadow: 0 10px 6px -6px var(--Font1);
    box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.5);
  }

  .card-title {
    text-align:center;
    color: var(--Font1)!important;
  }
  @media only screen and (max-width: 768px) {
    /* For mobile phones: */
    .item{
      padding:5px;
    }
    .main-work{
      margin:80% 0 0 0;  
      height: 100vh;  
      }
      .container{
        margin-top:10%;  
      }
}
  
`