import React from 'react';
import styled from 'styled-components';

const About = () => {
    return (
        <div className="my-5" style={{ height:'100vh'}} id="about">
        <AboutWrapper>
        <section className="main-sec">
           <h3 className="text-title">About me</h3>
               <p>Hello, I am Sachin. I am a Software Developer based in Melbourne, Victoria, Australia. I just finished my 
               Professional year program and internship and now learning new segments of Software engineering mainly focused on web 
               development. As a software engineer, I enjoy learning better ways to create seamles user experiences with clean and effcient code.
               Here are some of the techonologies I am working with recently.
               <p className="techs">Reactjs || Bootstrap 4 || Styled-Components || HTML || CSS</p>  </p> 
        </section>
        </AboutWrapper>
        </div>
    );
};

export default About;

const AboutWrapper=styled.div`

.main-sec{
    border-color:red;
    position:relative;
    margin-left:30%;
    transform: translate(-20%);
}
.main-sec p{
margin-top:5%;
}
.techs{
    color:var(--Font1);
}
@media only screen and (max-width: 768px) {
    /* For mobile phones: */
    .main-sec{
    margin-left:25%;   
    }
    .main-sec p{
    margin-top:10%;
    }
}
`