import React, { Component } from 'react';
import styled from 'styled-components';
import {Button1} from './Button';

class Home extends Component {
    render() {
        return (
            <HomeWrapper >
            <div id="home" style={{ height:'100vh'}}>
            <section className="main-div" >    
            <div className="landing-page" >
                <p className="p-h">Hi, My name is</p>
                <h1 className="p-h1">Sachin Macwan,</h1>
                <h2 className="p-h2"> I am a Software Developer</h2>
                <p className="p-h3">I love building softwares with beautiful interface</p>
                <Button1><a href={'mailto:s.macwan794@gmail.com'}>Contact Me </a></Button1>
            </div>
            <div>
                
            </div>
            </section>
            </div>
            </HomeWrapper>
        );
    }
}

export default Home;

const HomeWrapper = styled.div`


.landing-page {
    position: absolute;
    top:50%;
    left:28%;
    transform: translate(-20%,-50%);
    transition: opacity 3s;
    animation: fadeIn ease-in-out 1.3s;
}
.landing-page .p-h {
    color: var(--Font3) !important;
    font-size: 1.5rem;
}
.landing-page .p-h1 {
    color: var(--Font1) !important;
    font-family: "Courier", !important;
    font-size: 5rem;
}
.landing-page .p-h2 {
    color: var(--Font2) !important;
    font-family: "Courier", !important;
    font-size: 2.5rem;
}
.landing-page .p-h3 {
    color: var(--Font2) !important;
    font-family: "Courier", !important;
    font-size: 2.5rem;
}
@media only screen and (max-width: 768px) {
    /* For mobile phones: */
    .landing-page {
        left:25%;
        top:40%;
    }
    .landing-page .p-h, .landing-page .p-h3
    {
        font-size:calc(2em - 1rem);
    }
    .landing-page .p-h1 
    {
        font-size:calc(3.30em - 1rem);
    }
    .landing-page .p-h2
    {
        font-size:calc(2.9em - 1rem);
    }
}

@keyframes fadeIn{
    0%{
		opacity: 0;
		transform: translate3d(-20%, -58%, 0);
    }
    25%{
		opacity: 0;
		transform: translate3d(-20%, -54%, 0);
    }
    50%{
		opacity: 0;
		transform: translate3d(-20%, -53%, 0);
    }
    75%{
		opacity: 0;
		transform: translate3d(-20%, -53%, 0);
	}
    80%{
		opacity: 0.3;
		transform: translate3d(-20%, -52%, 0);
	}
    90%{
		opacity: 0.6;
		transform: translate3d(-20%, -51%, 0);
	}
	100%{
		opacity: 1;
		transform: translate3d(-20%,-50%, 0);
}
`