import React,{useEffect} from 'react';
import styled from 'styled-components';
import { animateScroll as scroll} from 'react-scroll'
import {Button1} from './Button';

function scrollToTop() {
    scroll.scrollToTop();
  }

const Contact = () => {
    
    return (
        <ContactWrapper>
        <div id='contact' className="main-div" style={{height:'100vh'}}>
        <link rel="stylesheet" href="https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.min.css"/>
        
                    <section className="main-sec mt-5">
                    <h3 className="text-title" >Contact me </h3>
                    <p >I am open to all opportunities. My inbox is always open if you have any 
                    questions of just want to say hi, I will try to get back to you</p>
                    <Button1><a href={'mailto:s.macwan794@gmail.com'}>Say Hello</a></Button1>
                    </section>
                    <div className="scroll-btn" >
                    <i class="fa fa-arrow-up" onClick={scrollToTop}></i>
                    </div>
                    <div className="social">
                        <ul>
                            <li><a href={'mailto:s.macwan794@gmail.com'} class="envelope"><i class="fa fa-envelope"></i></a></li> 
                            <li><a target="_blank" href="https://www.linkedin.com/in/sachin-macwan-727352163/" class="linkedin" rel="noopener noreferrer"><i class="fa fa-linkedin"></i></a></li>
                            <li><a target="_blank" href="https://github.com/Sach7n" class="github" rel="noopener noreferrer"><i class="fa fa-github"></i></a></li>
                        </ul>
                    </div>    
                    
        </div>
      
        </ContactWrapper>
    );
};

export default Contact;

const ContactWrapper=styled.div`
.main-div{
    height:100%;
}
.main-sec{
    position:relative;
    margin-left:30%;
    transform: translate(-20%);
}
.scroll-btn{
    max-width: 22px;
    position:absolute;
    margin-left:50%;
    margin-top:20%;
    cursor:pointer;
}
.scroll-btn i:hover{ 
    transform: scale(1.2);
    transition: all 0.3s ease;
}
.main-sec p{
    margin-top:5%;
}
.social{
    display:none;
}
.footer{
    position:relative;
    left:0;
    top:62%;
    height:10%;
    width: 100%;
    color:var(--Font1);
    text-align: center;
    z-index:0;
}
.bc{
    color:var(--Font2);
}
@media only screen and (max-width: 768px) {
    /* For mobile phones: */
    .main-div{
    margin-top:50%;
    }
    .main-sec{
    margin-top:40%;
    margin-left:25%;
    }
    .main-sec p{
    margin-top:10%;
    }
    .social{
        display:flex;
        position:relative;
        top:25%;
        left:0;
        text-align:center;
        width:100%;
        background:var(--MainBackground);
    }
    .social ul{
        align-items:center;
        display:flex;
        padding: 0 1rem;
        list-style:none;
        margin-left:22%;
    }
    .social li{
        padding:0 0.5rem;
        transform: scale(1.3);
        margin-left: 1.5rem;
    }
    .social li a{
        color: var(--Font2);
    }
    
    .social li:hover {
        background-color:transparent;
        color: var(--Font3);
      }
      .footer{
        top:55%;
        margin-left:50%;
        transform:translate(-50%);
      }
}
`